import React, { PureComponent } from 'react';
import styles from './CreateLink.module.scss';

import { inject, observer } from 'mobx-react';

import { Modal, ModalBody, ModalHeader } from '../Modal';

import Loader from '../../Loader';

import { GraphStoreType } from '../../../stores/graph/graphStore';
import { ModalStoreType } from '../../../stores/modalStore';

import CreateLinkDetail from '../CreateLinkDetail';

import { CommonStoreType } from '../../../stores/common/commonStore';

import Dropdown from '../../Dropdown';
import { ReactComponent as LineIcon } from './assets/line.svg';

type CreateLinkProps = {
  modalStore?: ModalStoreType;
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  [key: string]: any;
};

type CreateLinkState = {
  linkData: any;
  // Наличие признака активности группы "Кооперация"
  disabledCooperation: boolean;
  // Наличие признака активности группы "Согласованность"
  disabledConsistency: boolean;
  // Наименование заголовка для передачи в модальное окно по вводу детализированной информации о связи
  nameTitle: any;
  // Типы связей
  edgeTypesData: any;
  // Тип связи "Кооперация"
  typeCooperation: any;
  // Тип связи "Согласованность"
  typeConsistency: any;
  // Подтипы связи
  subTypes: any;
  initialSubTypes: any;
  // ID типа связи
  typeId: any;
  // ID подтипа связи
  subtypeId: any;
  typeLinkSelected: any;
  typeLinks: any;
  liveSearchValue: string;
};

const CustomDiv = (props: any) => {
  return <div>{props.children}</div>;
};

/**
 * Компонент модального окна по созданию пользовательской связи
 */
@inject('modalStore', 'graphStore', 'commonStore')
@observer
class CreateLink extends PureComponent<CreateLinkProps, CreateLinkState> {
  constructor(props: CreateLinkProps) {
    super(props);

    this.state = {
      disabledCooperation: true,
      disabledConsistency: true,
      linkData: [{}],
      nameTitle: '',
      edgeTypesData: [{}],
      typeCooperation: {},
      typeConsistency: [{}],
      subTypes: [{}],
      initialSubTypes: [],
      typeId: '',
      subtypeId: '',
      typeLinkSelected: {},
      typeLinks: [],
      liveSearchValue: '',
    };
  }

  typeLinks: any;

  componentDidMount() {
    const { graphStore, createLinkModeV3 } = this.props;
    // Получение типов связи
    const edgeTypesData = graphStore!.edgeTypesData;
    let typeLinks =
      edgeTypesData?.map((item: any) => ({ name: item.name, value: item.type_id })) || [];

    if (createLinkModeV3) {
      typeLinks = typeLinks?.filter((item: any) => item.value === 1) || [];
    }

    this.setState({
      typeLinks,
    });
  }

  /**
   * Изменяет состояние Вида связи
   */
  changeTypeLink = (selected: any) => {
    const { graphStore } = this.props;
    const edgeTypesData = graphStore!.edgeTypesData;

    this.setState({
      typeLinkSelected: {
        name: selected.name,
        value: selected.value,
      },
      nameTitle: '',
      typeId: '',
      subtypeId: '',
      liveSearchValue: '',
    });

    edgeTypesData.forEach((edgeType: { name: any; subtypes: any[] }) => {
      if (edgeType.name === selected.name) {
        const subTypes = edgeType.subtypes.sort((a: any, b: any) => {
          return a.type_id - b.type_id;
        });
        this.setState({
          typeCooperation: edgeType,
          initialSubTypes: subTypes,
          subTypes,
        });
      }
    });
  };

  /**
   * Изменяет состояние Подвид связи
   */
  changeSubtypeLink = (selected: any) => {
    const { typeLinkSelected } = this.state;

    this.setState({
      nameTitle: selected.name,
      typeId: typeLinkSelected.value,
      subtypeId: selected.type_id,
      liveSearchValue: '',
      subTypes: this.state.initialSubTypes,
    });
  };

  onSearchSubtypes = (value: string) => {
    this.setState({
      liveSearchValue: value,
      subTypes: this.state.initialSubTypes.filter((subType: any) =>
        subType.name.toLowerCase().includes(value.toLocaleLowerCase()),
      ),
    });
  };

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { onClose, graphStore, fromNode, toNode, commonStore, createLinkModeV3, modeLink, isFL } =
      this.props;
    const {
      state: { subTypes, nameTitle, subtypeId, typeLinkSelected, typeLinks, liveSearchValue },
    } = this;

    return (
      <Modal>
        <ModalHeader onClose={onClose}>Создание связи</ModalHeader>

        <ModalBody>
          {graphStore!.edgeTypesData ? (
            <div className={styles.body}>
              <div className={styles.containerMainInfo}>
                <LineIcon className={styles.lineIcon} />
                <div className={styles.containerNameINN}>
                  <span className={styles.headerNameINN}>Источник связи</span>
                  <div className={styles.detailsNameINN}>
                    <span>
                      <span>ИНН: </span>
                      <span className={styles.inn}>
                        {createLinkModeV3 && modeLink === 'from' ? fromNode.inn : fromNode.node.inn}
                      </span>
                    </span>
                    <span>
                      <span>Краткое наименование: </span>
                      <span
                        className={styles.name}
                        title={
                          createLinkModeV3 && modeLink === 'from'
                            ? fromNode.name
                            : fromNode.node.values[0]
                        }
                      >
                        {createLinkModeV3 && modeLink === 'from'
                          ? fromNode.name
                          : fromNode.node.values[0]}
                      </span>
                    </span>
                  </div>
                  <span className={styles.headerNameINN}>Получатель связи</span>
                  <div className={styles.detailsNameINN}>
                    <span>
                      <span>ИНН: </span>
                      <span className={styles.inn}>
                        {createLinkModeV3 && modeLink === 'to' ? toNode.name : toNode.node.inn}
                      </span>
                    </span>
                    <span>
                      <span>Краткое наименование: </span>
                      <span
                        className={styles.name}
                        title={
                          createLinkModeV3 && modeLink === 'to'
                            ? toNode.name
                            : toNode.node.values[0]
                        }
                      >
                        {createLinkModeV3 && modeLink === 'to'
                          ? toNode.name
                          : toNode.node.values[0]}
                      </span>
                    </span>
                  </div>
                </div>
              </div>

              <div className={styles.separateLine} />

              <div className={styles.data_container}>
                <Dropdown
                  itemsArray={typeLinks}
                  onSelect={this.changeTypeLink}
                  value={typeLinkSelected?.name}
                  label={'Вид связи'}
                  placeholder="Выберите значения"
                  theme={commonStore?.appTheme}
                  isArray
                />

                <Dropdown
                  itemsArray={subTypes}
                  onSelect={this.changeSubtypeLink}
                  value={nameTitle}
                  placeholder="Дополнительный параметр значения"
                  theme={commonStore?.appTheme}
                  isArray
                  disabled={!typeLinkSelected?.value}
                  onLiveSearch={this.onSearchSubtypes}
                  liveSearchValue={liveSearchValue}
                />
              </div>

              <div className={styles.separateLine} />

              <CreateLinkDetail
                fromNode={fromNode}
                toNode={toNode}
                nameTitle={nameTitle}
                typeId={typeLinkSelected?.value}
                subtypeId={subtypeId}
                createLinkModeV3={createLinkModeV3}
                modeLink={modeLink}
                isFL={isFL}
                onClose={onClose}
              />
            </div>
          ) : (
            <div className={styles.loader}>
              <Loader />
            </div>
          )}
        </ModalBody>
      </Modal>
    );
  }
}

export default CreateLink;
